


import { observable, action, computed, autorun } from "mobx";

import { axios } from "&";

class Staffs {
    @observable staffs = [];

    @observable avatar = "";

    @observable imageUrl = "";
    @action getStaffs = (keyword) => {

        axios({
            url: "/react/getOneStaff",
            params: { keyword: keyword }
        }).then(res => {
            console.log(res)
            this.staffs = res.data.result;
            this.imageUrl = res.data.avatar;
        })
    }
    @action changeEdu = (val) => {
        this.staffs.education = val
    }
    @action changeSex = (val) => {
        this.staffs.sex = val
    }
    @action changePart = val => {
        this.staffs.part = val
    }
    @action changeState = val => {
        this.staffs.state = val;
    }

    @action modificationStaff = obj => {
        axios({
            url: "/react/modificationStaff",
            data: obj,
            method: "POST",
        }).then(res => {
            this.staffs = res.data.result;
        })
    }
    
    @action changeImageUrl =(imageUrl)=>{

        this.imageUrl = imageUrl.replace(/public/,"http://localhost:1906/")

    }


}

export default new Staffs();