


import "./index.scss";

import { axios } from "&"

import { Input, Select, Upload, Icon, message, Button, DatePicker } from 'antd';

import Staffs from "../../../mobx/staffs";

import { observer } from "mobx-react";
import { format } from "util";
const { MonthPicker, RangePicker, WeekPicker } = DatePicker;
const { Search } = Input;
const { Option } = Select;
const warning = () => {
    message.warning('搜索框不能为空');
};

function getBase64(img, callback) {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
}

function beforeUpload(file) {
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
    if (!isJpgOrPng) {
        message.error('You can only upload JPG/PNG file!');
    }
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
        message.error('Image must smaller than 2MB!');
    }
    return isJpgOrPng && isLt2M;
}

@observer

export default class Chart extends Component {
    constructor() {
        super();
        this.state = {
            expand: false,
            loading: false,
            serial: "",
            name: "",
            multiple: false,
            imageUrl: ""
        }
    }

    componentDidMount() {

        let name = this.name.value;
        if (!!name) {
            axios({
                url: "/react/getImage",
                method: "POST",
                data: {
                    name,
                }
            }).then(res => {
                if (res.data.type == 1) {

                    this.setState({
                        imageUrl: res.data.avatar.replace(/public/, "https://fengkang.work:1906"),
                    })
                    return;
                } else {
                    this.setState({
                        imageUrl: require("@/assets/images/Pikachu.jpg")
                    })
                    return;

                }
            })
        }

    }

    handleChangeedu = (val) => {
        console.log(val)
        Staffs.changeEdu(val)
    }
    handleChangesex = (val) => {
        console.log(val)
        Staffs.changeSex(val)
    }
    handleChangepart = (val) => {
        console.log(val)
        Staffs.changePart(val)
    }
    handleChangestate = (val) => {
        console.log(val)
        Staffs.changeState(val)
    }

    handleChange = info => {


        if (info.file.status === 'uploading') {
            this.setState({ loading: true });
            return;
        }
        if (info.file.status === 'done') {
            // Get this url from response in real world.
            getBase64(info.file.originFileObj, imageUrl =>
                this.setState({
                    imageUrl,
                    loading: false,
                }),
                console.log(this.state.imageUrl)
            );
        }
        let file = info.file.originFileObj;
        const data = new FormData();
        data.append("avatar", file);

        axios({
            url: "/react/uploadAvatar",
            method: "POST",
            data: data,
        }).then(res => {
            if (res.data.type == 1) {
                this.setState({
                    imageUrl: res.data.avatar.replace(/public/, "https://fengkang.work:1906"),
                })
            }
        })


    };
    getkewyord(val) {
        console.log(val)
        let keyword = val;
        if (keyword) {
            Staffs.getStaffs(keyword)
            // Staffs.changeImageUrl(Staffs.imageUrl)
            console.log(Staffs.imageUrl)
            this.setState({
                name: Staffs.name,
                serial: Staffs.serial,
                imageUrl: Staffs.imageUrl.replace(/public/, "https://fengkang.work:1906"),
            })
            console.log(11)
        } else {
            warning();
            return;
        }

    }
    modificationStaff = () => {
        let obj = {};
        let oldName = Staffs.staffs.name;
        obj.name = this.name.value;
        obj.age = this.age.value;
        obj.tel = this.tel.value;
        obj.education = this.education.props.value;
        obj.sex = this.sex.props.value;
        obj.part = this.part.props.value;
        obj.state = this.states.props.value == "在职" ? false : true;
        obj.entrytime = this.entrytime.value;
        obj.oldName = oldName;
        if (this.name.value && this.age.value && this.tel.value && this.education.props.value && this.sex.props.value && this.part.props.value && this.entrytime.value) {
            Staffs.modificationStaff(obj)
        }
    }

    render() {
        const { staffs, getStaffs } = Staffs;
        console.log(staffs)
        const { name, serial, imageUrl } = this.state
        const uploadButton = (
            <div>
                <Icon type={this.state.loading ? 'loading' : 'plus'} />
                <div className="ant-upload-text">上传头像</div>
            </div>
        );

        return (

            <div className="record">
                <h2>
                    员工档案
                </h2>
                <Search
                    placeholder="输入员工姓名或工号"
                    onSearch={value => this.getkewyord(value)}
                    enterButton
                    style={{ width: "2rem" }}
                />
                <hr />

                <div className="up">
                    <div className="upLeft">
                        <ul>
                            <li>
                                <span>工号</span>
                                <input disabled ref={el => this.serial = el} defaultValue={staffs.serial || ""} type="text" />
                            </li>
                            <li>
                                <span>姓名</span>
                                <input ref={el => this.name = el} defaultValue={staffs.name || ""} type="text" />
                            </li>
                            <li>
                                <span>学历</span>
                                <Select
                                    value={staffs.education || "请选择学历"}
                                    style={{ width: 120 }}
                                    onChange={this.handleChangeedu}
                                    ref={el => this.education = el}
                                >
                                    <Option value="博士">博士</Option>
                                    <Option value="研究生">研究生</Option>
                                    <Option value="本科">本科</Option>
                                    <Option value="专科">专科</Option>
                                    <Option value="高中">高中</Option>
                                    <Option value="初中">初中</Option>
                                </Select>
                            </li>
                            <li>
                                <span>性别</span>
                                <Select
                                    value={staffs.sex || "性别"}
                                    style={{ width: 120 }}
                                    onChange={this.handleChangesex}
                                    ref={el => this.sex = el}
                                >
                                    <Option value="男">男</Option>
                                    <Option value="女">女</Option>
                                </Select>
                            </li>
                            <li>
                                <span>年龄</span>
                                <input type="number" defaultValue={staffs.age || ""} ref={el => this.age = el} />
                            </li>
                            <li>
                                <span>电话</span>
                                <input type="tel"
                                    ref={el => this.tel = el}
                                    maxLength={11}
                                    defaultValue={staffs.tel || ""}
                                />
                            </li>

                        </ul>
                    </div>
                    <div className="upRight">
                        <Upload
                            name="avatar"
                            listType="picture-card"
                            className="avatar-uploader"
                            showUploadList={false}
                            action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                            beforeUpload={beforeUpload}
                            onChange={this.handleChange}
                        >
                            {imageUrl ? <img src={imageUrl} alt="avatar" style={{ width: '100%' }} /> : uploadButton}
                        </Upload>
                    </div>
                </div>
                <div className="down">
                    <h2>岗位信息</h2>
                    <hr />
                    <ul>
                        <li>
                            <span>部门</span>
                            <Select
                                value={staffs.part || "请选择部门"}
                                style={{ width: 120 }}
                                onChange={this.handleChangepart}
                                ref={el => this.part = el}
                            >
                                <Option value="销售">销售</Option>
                                <Option value="产品">产品</Option>
                                <Option value="会计">会计</Option>
                                <Option value="人事">人事</Option>
                            </Select>
                        </li>
                        <li>
                            <span>状态</span>
                            <Select
                                value={staffs.state || "请选择状态"}
                                style={{ width: 120 }}
                                onChange={this.handleChangestate}
                                ref={el => this.states = el}
                            >
                                <Option value={staffs.state ? "离职" : "在职"}>离职</Option>
                                <Option value={staffs.state ? "在职" : "离职"}>在职</Option>
                            </Select>
                        </li>
                        <li>
                            <span>入职时间</span>
                            <input
                                type="text" defaultValue={staffs.entrytime || ""}
                                placeholder="请以2019/29/20格式输入"
                                ref={el => this.entrytime = el}
                            />
                        </li>
                    </ul>
                    <div className="btn">
                        <Button className="s-btn" onClick={this.modificationStaff} type="primary">提交修改</Button>
                        <Button className="s-btn" onClick={() => window.location.reload()} type="danger">重置档案</Button>
                    </div>
                </div>
            </div>
        )
    }

}